@font-face {
    font-family: 'icomoon';
    src:    url('../fonts/icomoon.eot?byvw7r');
    src:    url('../fonts/icomoon.eot?byvw7r#iefix') format('embedded-opentype'),
        url('../fonts/icomoon.ttf?byvw7r') format('truetype'),
        url('../fonts/icomoon.woff?byvw7r') format('woff'),
        url('../fonts/icomoon.svg?byvw7r#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

footer {
  background: $main-bg;
  width: 100%;
}

#footer-wrap {
  max-width: $site-width;
  margin: 0 auto;
  display: flex;
  flex-flow: row;
  padding: 1rem 0;
}

#footer-menu-desktop {


  a {
    display: block;
    text-decoration: none;
    font-size: 1rem;
  }
}

#footer-social {
  flex-grow: 2;
  text-align: right;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;

  a {
    font-size: 2rem;
    margin: 1rem;

    &:last-child {
      margin-right: 0;
    }
  }
}

#footer-copyright {
  padding: 1rem 0;
  max-width: $site-width;
  margin: 0 auto;
  color: $main-link-color;
}

@media (max-width: $site-width) {
  #footer-wrap {
    padding: 0.5rem;
  }

  #footer-copyright {
    padding: 0.5rem;
  }
}
